import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {State} from './named-timer.state-enum';
import {timer} from 'rxjs';
import {take} from 'rxjs/operators';
import {FormControl} from '@angular/forms';

@Component({
  selector: 'app-named-timer',
  templateUrl: './named-timer.component.html',
  styleUrls: ['./named-timer.component.css']
})

export class NamedTimerComponent implements OnInit {
  constructor() { }
  @Input('timerId') id: number;
  @Output('removeTimerEvent') removeTimerEvent: EventEmitter<number> = new EventEmitter();

  name = 'timer';
  timeString = '00:00:00';
  totalTime = 0;
  lastSampleTime = 0;
  state = State.Paused;
  playButtonIcon = 'play_circle_outline';
  options = ['ESP-', 'Environmental', 'Training', 'Meeting'];
  myControl = new FormControl();
  removeMe() {
    this.removeTimerEvent.emit(this.id);
  }

  isRunning(){
    return this.state === State.Playing;
  }

  reset() {
    this.totalTime = 0;
    this.buildTimeString();
  }

  update() {
    const now = Date.now();
    const timeDiff = now - this.lastSampleTime;
    this.lastSampleTime = now;
    if ( this.state === State.Playing) {
      this.totalTime += timeDiff;
      this.buildTimeString();
    }
  }

  buildTimeString() {
    const date = new Date(0, 0, 0, 0, 0, 0, this.totalTime);
    const seperator = ':';

    this.timeString =
      this.formatTime(date.getHours()) + seperator +
      this.formatTime(date.getMinutes()) + seperator +
      this.formatTime(date.getSeconds());
  }

  formatTime(time: number) {
    if (time > 9) {
      return '' + time;
    } else {
      return '0' + time;
    }
  }
  pausePlay() {
    if (this.state === State.Paused) {
      this.state = State.Playing;
      this.playButtonIcon = 'pause_circle_outline';
    } else {
      this.state = State.Paused;
      this.playButtonIcon  = 'play_circle_outline';
    }
  }

  ngOnInit() {
    this.name = 'timer' + this.id;
    this.update();

    timer(0, 50).pipe(take(Infinity))
      .subscribe(next => this.update());
  }
}
